.header {
  display: flex;
  justify-content: space-between;
  font-size: 18px;

  .header__link {
    @extend .header__link;
    display: inline-block;
    margin: 0 10px;
  }

  .header__brand {
    display: flex;
    align-items: center;
  }
  .header__logo {
    height: 56px;
  }
}

.header__link {
  text-transform: capitalize;
  color: white;
  text-decoration: none;
  transition: all 0.1s ease;

  &:hover {
    transform: scale(1.2);
  }
}

.sidebar {
  .sidebar__link {
    @extend .header__link;
  }
}